*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;

}

body {
  background: url('./sunset1.jpg')  center center/cover;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p{
  font-size:1.2rem;
}
h1{
  font-size:6rem;
}

.App{
width: 100%;
height: 100vh;
position: relative;
background-color: rgba(0,0,0,0.4);
color: rgb(255, 255, 255);
}

.App:before{
  content: '';
 
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.container{
  max-width: 700px;
  height: 500px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.App .top{
  
  width: 100%;
  margin: 1rem,auto;

}
.App .description{
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(270deg);
  font-size: 2rem;
}

.App .bottom{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.2);
  /* bottom: 50%; */
}

.bold{
  font-weight: 700;
}
.App .search{
  text-align: center;
  padding: 1rem;

}

.App input{
  padding: .7rem 1.2rem;
  font-size: 1rem;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.1);
  color: #f8f8f8;

}
::placeholder{
  color: #f8f8f8;
}